<template>
	<main></main>
</template>

<script>
import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { GUI } from 'dat.gui'

export default {
	data() {
		return {
			gui: new GUI(),
			renderer: new THREE.WebGLRenderer(),
			scene: new THREE.Scene(),
			camera: new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.1, 1000 ),
			loader: new GLTFLoader(),
			controls: null,
			loaded: false
		}
	},
	mounted() {
		this.gui.domElement.style.setProperty('display', 'none')
		this.renderer.outputEncoding = THREE.sRGBEncoding
		this.renderer.setSize(window.innerWidth, window.innerHeight)
		if (!this.loaded) this.$el.appendChild(this.renderer.domElement)

		const ambientLight = new THREE.AmbientLight( 0xffffff, 0.4 )
		this.scene.add( ambientLight )
		const dirLight = new THREE.DirectionalLight( 0xefefff, 1.5 )
		dirLight.position.set( 10, 10, 10 )
		this.scene.add( dirLight )

		this.controls = new OrbitControls(this.camera, this.renderer.domElement)

		this.loader.load('./spaceship.glb', gltf => {

			const object = gltf.scene

			const box = new THREE.Box3().setFromObject(object)
			const size = box.getSize(new THREE.Vector3()).length()
			const center = box.getCenter(new THREE.Vector3())

			this.camera.near = size / 100;
			this.camera.far = size * 100;
			this.camera.updateProjectionMatrix()
			this.camera.position.copy(center)
      this.camera.position.x += size / 2.0
      this.camera.position.y += size / 5.0
      this.camera.position.z += size / 2.0
      this.camera.lookAt(center)
			this.camera.updateProjectionMatrix()

			object.position.x += (object.position.x - center.x)
			object.position.y += (object.position.y - center.y)
			object.position.z += (object.position.z - center.z)

			const cubeFolder = this.gui.addFolder('Object')
			cubeFolder.add(object.rotation, 'x', 0, Math.PI * 2)
			cubeFolder.add(object.rotation, 'y', 0, Math.PI * 2)
			cubeFolder.add(object.rotation, 'z', 0, Math.PI * 2)
			cubeFolder.open()
			const cameraFolder = this.gui.addFolder('Camera')
			cameraFolder.add(this.camera.position, 'z', 0, 10)
			cameraFolder.open()

			this.scene.add(object)

			this.animate()

		}, undefined, error => {
			console.log(error)
		})

		this.loaded = true
	},
	methods: {
		animate() {
			requestAnimationFrame(this.animate)
			this.renderer.render( this.scene, this.camera )
		}
	}
}
</script>

<style>
html, body {
	overflow: hidden;
}
body {
	margin: 0 auto;
}
</style>
